.visually-hidden {
  position: absolute;
  left: -999em;
}
.featured-content  div.large-7.columns {
  padding-left: 1em;
}
#main-search, #mobile-search {
  width: 80%;
  border-radius: 0px;
  -webkit-border-top-left-radius: 4px;
  -webkit-border-bottom-left-radius: 4px;
  -moz-border-radius-topleft: 4px;
  -moz-border-radius-bottomleft: 4px;
  border-top-left-radius: 4px;
  border-bottom-left-radius: 4px;
  float: left;
  height: 32px;
}
.search .secondary.button {
  width: 20%;
  -webkit-border-top-right-radius: 4px;
  -webkit-border-bottom-right-radius: 4px;
  -moz-border-radius-topright: 4px;
  -moz-border-radius-bottomright: 4px;
  border-top-right-radius: 4px;
  border-bottom-right-radius: 4px;
  padding: 5px 10px;
  margin-bottom: 0px;
  height: 32px;
}

.discreet {
  // Color needs slightly changed for contrast ratio
  color: #767676;
}

.pull-right {
  float: right !important;
}
.pull-left {
  float: left !important;
}

.portal-type-audio  {
  .col-md-3 {
    width: 16.66666667%;
  }

  .col-md-9 {
    width: 83.33333333%;
    padding-top: 3em;
  }

  .col-md-9 p {
    margin-bottom: 5px;
    width: 80%;
  }
  .col-md-9 p.read-more {
    width: 10%;
    float: right;
  }
}

@media (max-width: 976px) {
 .portal-type-audio .col-md-3 {
    width: 16.66666667%;
  }
}

.feature-tile-row {
  background-color: white;
}
.feature-tile-expanded-container {
  position: absolute;
  left: -999em;
  display: inline;
  &.active {
    width: 100%;
    float: left;
    left: auto;
    position: relative;
  }
}

@media (min-width: 991px) {
  .mosaic-grid-row {
    position: relative;
  }
  .feature-tile-expanded-container.active {
    ~ .mosaic-position-two-thirds {
      position: absolute;
      top: 0px;
      left: 66.666666%;
    }
    ~ .mosaic-position-third {
      position: absolute;
      top: 0px;
      right: 33.333333%;
    }
  }
}

@media (max-width: 991px) {
  .feature-tile-expanded-container.active {
    float: none;
    width: auto;
  }
}

